<template>
  <div class="wdShouyi">
    <ul class="tab_list">
      <li @click="changedt()">
        <i :class="(searchInfo.sort===2||searchInfo.sort===1) ? 'active' : ''">时间</i>
        <div>
          <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===2 ? 'active' : ''"></span>
          <span class="iconfont icon-xiasanjiao" :class="searchInfo.sort===1 ? 'active' : ''"></span>
        </div>
      </li>
      <li @click="changemn()">
        <i :class="(searchInfo.sort===3||searchInfo.sort===4) ? 'active' : ''">积分</i>
        <div>
          <span class="iconfont icon-shangsanjiao" :class="searchInfo.sort===3 ? 'active' : ''"></span>
          <span class="iconfont  icon-xiasanjiao" :class="searchInfo.sort===4 ? 'active' : ''"></span>
        </div>
      </li>
      <li @click="popupVisible=true"><i>筛选</i> </li>
    </ul>

    <ul class="jf_list">
      <MyLoadMore :list='jflist'  @is-loadmore = 'loadmore' :isLoad='isLoad'>
      <li v-for="(item,index) in jflist" :key="index" @click="toDetail(item.id,item.type)">
        <div class="jf_detail">
          <div class="jfd_good">
            <span v-if="item.type==1">首佣</span>
            <span v-if="item.type==2">续佣</span>
            <span v-if="item.type==3">增员奖</span>
            <span v-if="item.type==4">管理奖</span>
          </div>
          <div class="jfd_num jfd_zhuchu">+{{item.money|moneyType}}</div>
        </div>
        <div class="jf_data">
          <div class="jfd_year">{{item.created_at}}</div>
            <div :class="{jfd_state:true,jfd_deal:!item.iscome}" v-if="item.process==3">提现处理中</div>
            <div  v-if="item.process==4" :class="item.process==4?'jfd_year':''">已提现</div>
        </div>
      </li>
      </MyLoadMore>
    </ul>

    <mt-popup
      v-model="popupVisible"
      position="right"
    >
      <div class="po_warp">
        <form @submit.prevent="popupVisible=false;getIncome()">
          <div class="bottom_type">
            <van-button type="default" @click.prevent="resetSearch">重置</van-button>
            <van-button type="default" native-type="submit">确认</van-button>
          </div>
          <div class="info">时间</div>
          <div class="time_area">
            <div class="low_time" @click="$refs.pickerone.open()">{{searchInfo.time_down}}<i class="iconfont icon-rili"></i></div>
            <span></span>
            <div class="high_time" @click="$refs.pickertwo.open()">{{searchInfo.time_top}}<i class="iconfont icon-rili"></i></div>
          </div>
          <div class="info">金额区间</div>
          <div class="money_area">
            <input type="number" min="0" v-model="searchInfo.money_down" placeholder="最低金额">
            <span></span>
            <input type="number" :min="searchInfo.money_down" v-model="searchInfo.money_top" placeholder="最高金额">
          </div>
          <div class="info">类别</div>
          <div>
            <ul class="type_list">
              <li v-for="(item,index) in type_list" :key="index" :style="{'color':searchInfo.type===item.status?'#3C7FEE':''}" @click="()=>{$set(searchInfo,'type',item.status)}">
                {{item.typeName}}
              </li>
            </ul>
          </div>
        </form>
      </div>
    </mt-popup>
    <a href="" class="kefu iconfont icon-kefu"></a>
    <mt-datetime-picker
      ref="pickerone"
      type="date"
      year-format="{value} 年"
      month-format="{value} 月"
      date-format="{value} 日"
      :startDate="new Date('2000/1/1')"
      :endDate="new Date()"
      @confirm="handleConfirmone">
    </mt-datetime-picker>
    <mt-datetime-picker
      ref="pickertwo"
      type="date"
      year-format="{value} 年"
      month-format="{value} 月"
      date-format="{value} 日"
      :startDate="new Date(searchInfo.time_down)"
      :endDate="new Date()"
      @confirm="handleConfirmtwo">
    </mt-datetime-picker>
  </div>
</template>

<script>
import { apiGet } from '../common/js/api';
import  MyLoadMore  from '@/common/MyLoadMore'
  export default {
    name: "jf_mingxi",
    components:{ MyLoadMore },
    data() {
      return {
        show: false,
        lowDate:false,
        isLoad:true,
        searchInfo:{
          // timeSort:1,//0表示时间正序，1倒序,-1表示不参加条件
          // scoreSort:-1,//0表示积分正序，1倒序， -1表示不参加条件
          sort:"",
          type:"",//类型
          page:1,
          per:10
        },
        type_list:[
          {
            typeName:"首佣",
            status:1
          },
          {
            typeName:"续佣",
            status:2
          },
          {
            typeName:"增员奖",
            status:3
          },
          {
            typeName:"管理奖",
            status:4
          },
          // {
          //   typeName:"充值",
          //   status:4
          // },
          // {
          //   typeName:"兑换",
          //   status:5
          // },
        ],
        popupVisible: false,
        jflist:[],
      }
    },
    created(){
      this.getIncome()
    },
    methods: {
      //获取我的收益列表
      getIncome(){
        apiGet("/user/income-list",{
          ...this.searchInfo,
          accessToken:this.$cookie.get('accessToken')
          // accessToken:"3HzxiZdbTu1dcWkJN96N_TULIae8DIZ_"
        }).then(res=>{
          console.log(res)
          this.jflist = res.data.data;
        })
      },
      //加载更多
      loadmore(){
        this.$set(this.searchInfo,'page',++this.searchInfo.page)
        apiGet("/user/income-list",{ ...this.searchInfo, accessToken:this.$cookie.get('accessToken')}).then(res => {
          if (res.status === 200) {
            this.jflist = this.jflist.concat(res.data.data);
            if(res.data.data.length< 10) {
              this.isLoad = false;
            } else {
              this.isLoad = true
            }
          }
        })

      },
      //按时间正反排序
      changedt(){
        this.searchInfo.page=1;
        this.isLoad=true;
        const { sort } = this.searchInfo
        this.$set(this.searchInfo,'sort', sort===1? 2 : 1)
        console.log(this.searchInfo.sort);
        this.getIncome()
      },
      //按分数该地排序
      changemn(){
        this.searchInfo.page=1;
        this.isLoad=true;
        const { sort } = this.searchInfo
        this.$set(this.searchInfo,'sort', sort===3? 4 : 3)
        this.getIncome()
      },
      //重置搜索条件
      resetSearch(){
        this.searchInfo.page=1;
        this.isLoad=true;
        this.popupVisible = false;
        this.searchInfo = {
          // timeSort:-1,//0表示时间正序，1倒序,-1表示不参加条件
          // scoreSort:-1,//0表示积分正序，1倒序， -1表示不参加条件
          sort:"",
          type:"",//类型
        };
        this.getIncome();
      },
      //开始时间
      handleConfirmone (date) {
        console.log(date);
        this.$set(this.searchInfo,'time_down',date.toLocaleDateString().replace(/\//g,"-"))
      },
      //结束时间
      handleConfirmtwo (date) {
        console.log(date);
        this.$set(this.searchInfo,'time_top',date.toLocaleDateString().replace(/\//g,"-"))
      },
      //跳转收益详情
      toDetail(incomeId,type){
        this.$router.push({
          path:"IncomeDetail",
          query:{
            incomeId,
            type
          }
        })
      }
    }
  }
</script>

<style scoped lang="scss">
  .active{
    color: #3C7FEE !important;
  }
  .wdShouyi{
    // padding-bottom: 0.6rem;
    background: #E6E9ED;
    height: 100%;
    display:flex;
    flex-direction: column;
    .mint-popup-right{
      width: 70%;
      height: 100%;
      background: white;
      .po_warp{
        position: relative;
        height: 100%;
        overflow: auto;
        .info{
          height: 0.31rem;
          background: #efefef;
          line-height: 0.31rem;
          text-indent: 0.13rem;
          font-size: 0.15rem;
          color: #9a9a9a;
        }
        .time_area,.money_area{
          padding-top: 0.21rem;
          overflow: hidden;
          padding-bottom: 0.2rem;
          span{float: left;margin: 0.15rem 0.06rem;width: 0.16rem;height: 1px;background: #bcbcbc}
          .low_time{margin-left: 5%}
          .low_time,.high_time,input{
            width: 40%;
            font-size: 0.13rem;
            color: #9a9a9a;
            line-height: 0.31rem;
            border:1px solid rgba(188,188,188,1);
            border-radius:0.04rem;
            height: 0.31rem;
            text-indent: 0.09rem;
            float: left;
            i{float: right;color: #3C7FEE;font-size: 0.15rem;margin-right: 0.02rem}
          }
          input:nth-of-type(1){
            margin-left: 5%;
          }
        }
        .type_list{
          display: flex;
          padding-bottom: 0.3rem;
          flex-wrap: wrap;
          overflow: auto;
          li{
            width: 42%;
            height: 0.5rem;
            color: #333;
            font-size: 0.17rem;
            margin-left: 0.13rem;
            margin-top: 0.2rem;
            line-height: 0.5rem;
            text-align: center;
            background:rgba(229,232,235,0.5);
            border-radius:0.04rem;
          }
        }
        .bottom_type{
          position: fixed;
          bottom: 0;
          display: flex;
          width: 100%;
          font-size: 0.19rem;
          height: 0.47rem;
          button{
            flex: 1;
            text-align: center;
            line-height: 0.47rem;
          }
          button:nth-of-type(1){
            color: #3C7FEE;
            border: none;
            border-top: 1px solid #f5f5f5;
          }
          button:nth-of-type(2){
            color: white;
            background:linear-gradient(-90deg,rgba(60,127,238,1),rgba(103,154,237,1));
          }
        }
      }

    }
    .tab_list{
      display: flex;
      justify-content: space-between;
      height:0.47rem;
      width: 100%;
      background: white;
      li{flex: 1;line-height: 0.47rem;text-align: center;font-size: 0.19rem;
        display: flex;
        justify-content: center;
        i{font-style: normal}
        div{
          width: 0.3rem;
          display: flex;
          flex-direction: column;
          line-height: 0.34rem;
          span{
            font-size: 0.2rem;
            display: block;
            width: 0.3rem;
            color: #bcbcbc;
            height: 0.1rem;
          }
          .icon-shangsanjiao{
            font-size: 0.16rem;
          }

        }
      }
    }
    .jf_list{
      width: 3.5rem;
      flex:1;
      overflow: auto;
      background: white;
      margin: 0.17rem auto;
      border-radius: 0.04rem;
      li{
        border-bottom: 1px solid #f5f5f5;
        width: 3.42rem;
        height:0.8rem;
        margin: 0 auto;
        .jf_detail,.jf_data{
          display: flex;
          justify-content: space-between;
          .jfd_good{
            margin-top: 0.15rem;
            margin-left: 0.1rem;
          }
          .jfd_good,.jfd_year{
            font-size:.19rem;
            font-weight:400;
            color:rgba(51,51,51,1);
            margin-left: 0.12rem;
          }
          .jfd_year{
            font-size: 0.15rem;
            color: #9a9a9a;
            line-height: 0.4rem;
            span{
              margin-left: 0.13rem;
            }
          }
          .jfd_num{line-height: 0.43rem;color: #CC1C1C}
          .jfd_zhuchu{color: #CC1C1C}
          .jfd_num,.jfd_state{width: 30%;text-align: right;font-size: 0.17rem}
          .jfd_state{
            font-size: 0.13rem;
            color: #9a9a9a;
            line-height: 0.34rem;
          }
          .jfd_deal{
            color: #3C7FEE;
          }
        }
      }
    }
    .kefu{
      width: 0.52rem;
      height: 0.52rem;
      background:linear-gradient(-90deg,rgba(70,133,238,1),rgba(155,193,255,1));
      border-radius:50%;
      box-shadow:0px 5px 10px 0px rgba(60,127,238,0.42);
      position: fixed;
      bottom: 1.5rem;
      display: block;
      text-align: center;
      line-height: 0.52rem;
      font-size: 0.28rem;
      color: white;
      right: 0.11rem;
    }
  }
</style>



